.Footer{
    color:whitesmoke;
    background-color: rgb(240,240,240,0.3);
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    margin: auto;
    margin-top: 100px;
    width: min(500px,80vw);
}
.Footer a{
    transition: 0.15s;
    color:red;
    padding: 20px;
}
.Footer a:hover{
    transform: scale(1.2);
}
a.social {
    margin: 0 1rem;
    transition: transform 100ms;
    display: inline-block;
  }

a.social:hover {
    transform: translateY(-5px);
  }