.NavBar{
    font-size:22px;
   
    background-color: #000000;
    width: 100%;
    color:whitesmoke;
    box-shadow: 0 3px 4px black;
    padding: 20px;
    display: block;
}
.DarkModeIcon{
    font-size:20px;

}
.header{
    text-decoration: none;
    padding: 0.25em 1em;
    display: inline;
    color:aliceblue;
    float:left;
    vertical-align: middle;
    color:rgb(0, 89, 255);
}

.NavBar ul{
    text-decoration: none;
    display: inline;
}
.NavbarElement{
    text-decoration: none;
    padding: 0.1em 2em;
    display: inline;
    color:whitesmoke;
    transition: 0.15s;
    float:center;
    vertical-align: middle;
    margin-left: 20px;
    
}

.NavbarElement:hover,.NavbarElement:focus{
    color:rgb(15, 78, 180);
    transform: scale(1.1);
    background-color: white;
    border-radius: 20px;
    border:solid blue 2px;

}
.NavBar button{
    border: none;
    color:whitesmoke;
    background-color: black;
    font-size: 25px;
}
.Active{
    color:rgb(15, 78, 180);
    background-color: white;
    border-radius: 20px;
    border:solid blue 2px;
}
 