.SideBar{
    font-size:20px;
    height: 100%;
    background-color: #000000;
    color:whitesmoke;
    box-shadow: 0 3px 4px black;
    padding: 20px;

  max-width: 40%;
    z-index: 1000;
    
    position: fixed;
    left: 0;
    top:0;
}


.SideBar ul{
    text-decoration: none;
    margin: 0;
    list-style: none;
}
.SideBarElement{
    text-decoration: none;
    padding: 20px;
    width: 100%;
    display: inline-block;
    color:whitesmoke;
    transition: 0.15s;
    margin: 20px;
    margin-left: -25px;

  
}

.SideBarElement:hover,.SideBar:focus{
    color:rgb(15, 78, 180);
  
    background-color: aliceblue;


}
.Active1{
    background-color: whitesmoke;
    color:blue;
}