.CardElement{
  
    color:black;
}
.CardHeader{
   
    color:whitesmoke;
    font-weight: 900;

}
.Card{
    background-color: rgb(240,240,240,0.3);
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    color:pink;
    margin-top: 100px;
    width: min(500px,45vw);
    display: inline-block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
}
.CardImage{
    height: min(300px,30vw);
    width: min(400px,40vw);
    padding: 20px;
}
.Card:hover{
    transform: scale(1.1);
}
.Card a{
    overflow: wrap;
}
@media (max-width:600px)
{
    .Card{
        margin-left: 10px;
        margin-right: 10px;
        width: 95%;
    }
}