.Resume{
    background-color: rgb(240,240,240,0.3);
    border-radius: 5px;
    padding: 20px;
    margin: auto;
    text-align: left;
    width: min(500px,50vw);
    margin-top: 100px;
    color:whitesmoke;
}
.Resume li{
    padding: 5px;
    font-size: 16px;
}
.listHeader{
    font-size: 20px;
    font-weight: bold;
}
@media (max-width:600px) {
    .Resume{
        width: 95%;
        padding: 10px;
    }
    .Resume ul{
    width: 95%;
    }
    .Resume li{
        width: 95%;
    }
}